        this.$store.commit('setMbtiResult', res.data);
        this.result = this.$store.getters.getPersonalities.filter(p => p.sigle.toLowerCase() === res.data.toLowerCase()).pop();
        console.log(this.result);
        this.loading = false;
        this.$store.dispatch('sendProfileToDB');
      }).catch(err => {
        console.log(err);
        this.loading = false;
      });
