import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
import * as axios from 'axios'

Vue.use(Vuex);

const vuexLocal = new VuexPersistence({
  storage: window.localStorage
});

export default new Vuex.Store({
  state: {
    result: {
      mbti: null,
      bf: null
    },
    personalities: [
      { title: 'Architecte', desc: 'On se sent seul en haut et, comme ils sont un des types de personnalit les plus rares et les plus capables sur le plan stratgique, les Architectes ne le savent que trop bien. Les Architectes forment juste deux pourcent de la population et les femmes de ce type de personnalit sont particulirement rares, car elles ne forment que 0,8 % de la population. Pour les Architectes, il est souvent difficile de trouver des individus de mme sensibilit qui soient capables de suivre leur implacable intellectualisme et leurs manoeuvres de joueurs dchec. Les gens qui ont le type de personnalit  Architecte  sont imaginatifs mais catgoriques, ambitieux et pourtant secrets, tonnamment curieux, mais ils ne gaspillent pas leur nergie.', img: 'intj-architect', sigle: 'INTJ' },
      { title: 'Logicien', desc: 'Le type de personnalit  Logicien  est assez rare, car il ne constitue que trois pourcent de la population, ce qui est incontestablement une bonne chose pour eux, car rien ne les attristerait plus que dtre  ordinaires . Les Logiciens senorgueillissent de leur inventivit et de leur crativit, de leur perspective hors du commun et de la vigueur de leur intellect. Habituellement connus sous les traits du philosophe, de larchitecte ou du professeur rveur, les Logiciens ont t  lorigine de nombreuses dcouvertes scientifiques tout au long de lhistoire.', img: 'intp-logician', sigle: 'INTP' },
      { title: 'Commandant', desc: 'Les Commandants sont des leaders ns. Les gens qui ont ce type de personnalit incarnent les dons du charisme et de la confiance, et dgagent de lautorit dune manire qui rassemble les foules sous lgide dun but commun. Cependant,  la diffrence de leurs homologues plus sentimentaux, les Commandants se caractrisent par un niveau de rationalit souvent implacable. Ils utilisent leur dynamisme, leur dtermination et leur esprit vif pour accomplir le but quils se sont fix, quel quil soit. Ils ne reprsentent que trois pourcent de la population et cest peut-tre une bonne chose car, autrement, ils pourraient craser les types de personnalit plus timides et sensibles qui constituent une grande partie du reste du monde ; cela dit, il nous faut remercier les Commandants pour avoir cr beaucoup des entreprises et des institutions que nous considrons quotidiennement comme allant de soi.', img: 'entj-commander', sigle: 'ENTJ' },
      { title: 'Innovateur', desc: 'Le type de personnalit  Innovateur  est lavocat du diable ultime qui prospre en dcomposant les arguments et les croyances et en laisse les lambeaux flotter au vent pour que tous puissent les voir. Les Innovateurs ne le font pas parce quils essaient datteindre un objectif plus profond ou un but stratgique, mais pour la simple raison que cest amusant. Personne naime plus le processus de lutte mentale que les Innovateurs, car il leur donne loccasion dexercer leur bel esprit rapide et fluide, la large somme de connaissances quils ont accumule et la capacit de relier entre elles des ides disparates pour prouver quils ont raison.', img: 'entp-debater', sigle: 'ENTP' },
      { title: 'Avocat', desc: 'Le type de personnalit  Avocat  est trs rare et constitue moins de un pourcent de la population, mais cela ne les empche pas de laisser leur empreinte sur le monde. Ils ont un sens inn de lidalisme et de la morale, mais leur dtermination et leur fermet les diffrencie des autres types de personnalit. Les Avocats ne sont pas des rveurs inactifs, mais des gens capables de prendre des mesures concrtes pour raliser leurs buts et laisser un impact durablement positif.', img: 'infj-advocate', sigle: 'INFJ' },
      { title: 'Mdiateur', desc: 'Les Mdiateurs sont de vrais idalistes qui recherchent toujours un soupon de bien, mme chez les pires personnes ou dans les pires vnements, et recherchent des moyens damliorer les choses. Bien quils puissent tre perus comme tant des gens calmes, rservs ou mme timides, les Mdiateurs ont une flamme et une passion intrieures qui peuvent vraiment briller. Comme ils ne constituent que 4 % de la population, le risque de se sentir incompris est malheureusement lev pour les gens du type de personnalit  Mdiateur , mais sils trouvent des gens de mme sensibilit queux avec qui passer leur temps, lharmonie quils ressentiront sera une fontaine de joie et dinspiration.', img: 'infp-mediator', sigle: 'INFP' },
      { title: 'Protagoniste', desc: 'Les Protagonistes sont des leaders ns, pleins de passion et de charisme. Ils constituent environ deux pourcent de la population et sont souvent nos politiciens, nos coachs et nos enseignants. Ils tendent la main aux autres et les inspirent pour quils accomplissent et fassent le bien dans le monde. Avec une confiance naturelle qui gnre de linfluence, les Protagonistes ont beaucoup de fiert et de joie  aider les autres  travailler ensemble pour samliorer et amliorer leur communaut.', img: 'enfj-protagonist', sigle: 'ENFJ' },
      { title: 'Inspirateur', desc: 'La personnalit  Inspirateur  est un vritable esprit libre. Ils sont souvent lme dune soire mais,  la diffrence des Explorateurs, ils sont moins intresss par lexcitation et le plaisir purs du moment prsent que par lapprciation des connexions sociales et motionnelles quils tablissent avec autrui. Charmants, indpendants, nergiques et compatissants, les 7 % de la population quils reprsentent peuvent certainement se reprer dans nimporte quelle foule.', img: 'enfp-campaigner', sigle: 'ENFP' },
      { title: 'Logisticien', desc: 'On considre le type de personnalit  Logisticien  comme le plus courant, car il constitue environ 13 % de la population. Leurs caractristiques intrinsques dintgrit, de logique pragmatique et dinfatigable dvouement envers le devoir font des Logisticiens le coeur vital de nombreuses familles, aussi bien que des organisations qui soutiennent les traditions, les rgles et les normes comme les cabinets davocat, les organismes de rglementation et les militaires. Les gens qui ont le type de personnalit  Logisticien  aiment tre responsables de leurs actions et senorgueillissent du travail quils font. Quand ils travaillent pour atteindre un but, les Logisticiens ne comptent pas leur temps et leur nergie pour achever chaque tche pertinente avec exactitude et patience.', img: 'istj-logistician', sigle: 'ISTJ' },
      { title: 'Dfenseur', desc: 'Le type de personnalit des Dfenseurs est tout  fait unique, car beaucoup de leurs qualits dfient toute dfinition de leurs traits individuels. Bien que comprhensifs, les Dfenseurs peuvent tre farouches lorsquils doivent protger leur famille ou leurs amis. Bien que calmes et rservs, ils ont un sens solide du contact humain et des relations humaines approfondies. Bien quils recherchent la stabilit et la scurit, ils sont souvent rceptifs au changement et aux nouvelles ides. Comme tant de choses, les gens qui ont le type de personnalit  Dfenseur  sont plus que la somme de leurs parties et cest la manire dont ils utilisent ces points forts qui dfinit qui ils sont.', img: 'isfj-defender', sigle: 'ISFJ' },
      { title: 'Directeur', desc: 'Les Directeurs sont les reprsentants de la tradition et de lordre. Ils utilisent leur comprhension de ce qui est bien, mal et socialement acceptable pour rassembler les familles et les communauts. Les gens qui ont le type de personnalit  Directeur  adoptent les valeurs dhonntet, de dvouement et de dignit, sont apprcis pour la clart de leurs conseils et heureux de montrer la voie quand la route est difficile. Les Directeurs sont fiers de rassembler les gens. Ils assument souvent le rle dorganisateurs communautaires et travaillent dur pour rassembler tout le monde dans le cadre de la clbration dvnements locaux qui leur sont chers ou de la dfense des valeurs traditionnelles qui maintiennent la cohsion des familles et des communauts.', img: 'estj-executive', sigle: 'ESTJ' },
      { title: 'Consul', desc: 'Les gens qui partagent le type de personnalit  Consul  sont, faute dun meilleur terme, populaires, ce qui est logique, vu que cest aussi un type de personnalit trs rpandu qui reprsente douze pourcent de la population. Au lyce, les Consuls sont les majorettes et les quarterbacks, ceux qui donnent le ton, captent la lumire des projecteurs et mnent leur quipe vers la victoire et la clbrit. Plus tard dans leur vie, les Consuls aiment encore soutenir leurs amis et ceux quils aiment, organiser des rencontres sociales et faire de leur mieux pour sassurer que tout le monde est heureux.', img: 'esfj-consul', sigle: 'ESFJ' },
      { title: 'Virtuose', desc: 'Les Virtuoses adorent explorer des mains et des yeux, toucher et examiner le monde qui les entoure avec un rationalisme calme et une curiosit pleine dallant. Les gens qui ont ce type de personnalit sont des Faiseurs ns qui voluent dun projet  un autre, construisent des choses utiles et des choses superflues pour le plaisir et apprennent de leur environnement au fur et  mesure. Souvent mcaniciens et ingnieurs, le plus grand plaisir des Virtuoses est de se salir les mains en dmontant les choses et en les remontant un petit peu mieux quelles ntaient auparavant.', img: 'istp-virtuoso', sigle: 'ISTP' },
      { title: 'Aventurier', desc: 'Les types de personnalit  Aventurier  sont de vrais artistes, mais pas forcment dans le sens typique o ils sortiraient peindre de joyeux petits arbres. Cependant, assez souvent, ils en sont parfaitement capables. Ils ont plutt tendance  utiliser lesthtique, le design et mme leurs propres choix et actions pour repousser les limites des conventions sociales. Les Aventuriers aiment bouleverser les attentes traditionnelles avec des expriences sur la beaut et le comportement. Il y a des chances pour quils aient plus dune fois exprim lide :  Ne me rangez pas dans un tiroir ! ', img: 'isfp-adventurer', sigle: 'ISFP' },
      { title: 'Entrepreneur', desc: 'Les types de personnalit  Entrepreneur  ont toujours un impact sur leur environnement immdiat. La meilleure faon de les reprer lors dune fte est de chercher le tourbillon de gens qui voluent autour deux alors quils passent de groupe en groupe. Riantes et distrayantes avec un humour brusque et truculent, les Entrepreneurs aiment attirer lattention. Si on demande  un membre du public de monter sur scne, les Entrepreneurs se portent volontaires, ou dsignent un ami timide comme volontaire.', img: 'estp-entrepreneur', sigle: 'ESTP' },
      { title: 'Amuseur', desc: 'Si une personne est susceptible de se mettre spontanment  chanter et  danser, cest le type de personnalit  Amuseur . Les Amuseurs se laissent emporter par lexcitation du moment prsent et veulent que tous les autres ressentent les choses de la mme faon. Aucun autre type de personnalit nest aussi gnreux avec son temps et son nergie que les Amuseurs quand il sagit dencourager les autres, et aucun autre type de personnalit ne le fait avec un style aussi irrsistible.', img: 'esfp-entertainer', sigle: 'ESFP' }
    ]
  },
  getters: {
    getMbtiResult(state) {
      return state.result.mbti;
    },
    getBfResult(state) {
      return state.result.bf;
    },
    getPersonalities(state) {
      return state.personalities;
    },
    isMobileDevice() {
      return window.innerWidth < 1000;
    }
  },
  mutations: {
    setMbtiResult(state, mbti) {
      if (mbti)
        state.result.mbti = mbti.toUpperCase();
      else
        state.result.mbti = null;
    },
    setBfResult(state, bf) {
      state.result.bf = bf;
    }
  },
  actions: {
    sendProfileToDB({ state }) {
      if (!state.result.mbti || !state.result.bf)
        return;

      axios.post(process.env.VUE_APP_API_URL + '/insert', {
        mbti: state.result.mbti,
        bf: state.result.bf
      }).then(res => {
        console.log(res);
      }).catch(err => {
        console.log(err);
      });
    }
  },
  plugins: [vuexLocal.plugin]
});
